input {
  border: 1px solid #e4e4e4;
  width: 100%;
  padding: 15px 10px;
  background: white;
  font-family: 'Open Sans', sans-serif;
  border-radius: 3px; }

input[type="file"] {
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 1;
  position: relative;
  cursor: pointer; }

.file {
  border: 1px solid #e4e4e4;
  background: white;
  width: 100%;
  font-family: "Open Sans", sans-serif;
  position: absolute;
  left: 0;
  bottom: 0;
  top: 0;
  right: 0; }

.button {
  background: #53b5fc;
  position: absolute;
  color: white;
  max-width: 100px;
  height: 100%;
  padding: 15px 0;
  width: 100%;
  right: 0;
  text-align: center; }
  .button img {
    vertical-align: middle;
    margin-right: 5px; }
  .button span {
    vertical-align: middle; }

.choose_file {
  position: relative; }



