.drop-down {
  font-size: 13px;
  border: 1px solid #e4e4e4;
  width: 100%;
  background: white;
  font-family: "Open Sans", sans-serif;
  border-radius: 3px;
  overflow: hidden;
  position: relative; }
  .drop-down .select {
    width: 100%;
    background: url("/images/arrow_down.png") no-repeat;
    background-position: right 15px center; }
    .drop-down .select select {
      background: transparent;
      border: none;
      padding: 0;
      width: 100%;
      position: relative;
      z-index: 10;
      cursor: pointer;
      -webkit-appearance: none;
      padding: 16px 10px; }



